import chroma from 'chroma-js';
import { START_YEAR, END_YEAR } from './constants';

export const colorScale = chroma
  .scale(['#2A4858', '#F8E800'])
  .mode('lch')
  .colors(END_YEAR - START_YEAR + 1);

export const maxCoord = array => {
  const values = [];
  array.forEach(coordinate => {
    values.push(coordinate[0]);
    values.push(coordinate[1]);
  });
  return Math.max(...values);
};

const capitalize = str => str.charAt(0).toUpperCase() + str.slice(1);

export const keyToDataName = key => {
  const parts = key.split('_');
  parts[0] = capitalize(parts[0]);
  return parts.join(' ');
};

export const flattenFields = fields => ({
  names: fields.map(f => f.name),
  peerVisibility: fields.reduce((acc, { name, showPeers }) => {
    acc[name] = showPeers;
    return acc;
  }, {}),
});
